.portraitalumni-wrapper {
  display: contents;
}

.portrait-alumni {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  gap: 0.1rem;
  margin: 0.1rem 0;
  padding: 0.1rem;
  border-left: 4px solid #ccc;
  background-color: #f9f9f9;
  font-size: 1rem;
  line-height: 1.4;
}


.portraitalumni[data-style="small"] {
  width: 100px;
}

.portraitalumni[data-style="tiny"] {
  flex-direction: row;
  gap: 15px;
  width: unset;
  text-align: left;
}

.portraitalumni[data-style="tiny"] .portraitalumni-role {
  display: none;
}

.portraitalumni-text {
  display: flex;
  flex-direction: column;
  line-height: calc(var(--spacing) - 0.4);
}

.portraitalumni-name {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
}
